import { ConnectedRouter } from 'connected-react-router';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import React from 'react';
import ReactDOM from 'react-dom';
import ReactGA from 'react-ga4';
import { Provider } from 'react-redux';
import Router from './Router';
import configureStore, { history } from './configureStore';
import rootSaga from './sagas/root';
import * as serviceWorker from './serviceWorker';

// import Global CSS
import { Modal } from 'antd';
import './App.css';
import { getServerConnection } from './utils/helper/utilities';

dayjs.extend(relativeTime);

const store = configureStore();
store.runSaga(rootSaga);

ReactGA.initialize('G-3YXW8S2K7X');

ReactDOM.render(
	<Provider store={store}>
		<ConnectedRouter history={history}>
			<Router history={history} />
		</ConnectedRouter>
	</Provider>,
	document.getElementById('root'),
	document.addEventListener('DOMContentLoaded', getServerConnection),
	document.addEventListener('contextmenu', (e) => {
		e.preventDefault();
		Modal.error({
			width: 500,
			title: 'PERHATIAN !!!',
			content: (
				<p className="text-base leading-snug text-gray-800">
					Kami mendeteksi bahwa anda ingin mengakes fitur yang dilarang. <b className="text-red-500 uppercase">Anda tidak diizinkan</b>{' '}
					untuk mengakes fitur tersebut.
				</p>
			)
		});
		return false;
	}),
	document.addEventListener('keydown', (e) => {
		if (
			// e.keyCode === 116 ||
			// e.keyCode === 123 ||
			e.button === 23 ||
			(e.ctrlKey && e.keyCode === 116) ||
			(e.ctrlKey && e.shiftKey && e.keyCode === 73) ||
			(e.ctrlKey && e.shiftKey && e.keyCode === 80) ||
			(e.ctrlKey && e.keyCode === 85) ||
			(e.shiftKey && e.keyCode === 81)
		) {
			e.preventDefault();
			Modal.error({
				width: 500,
				title: 'PERHATIAN !!!',
				content: (
					<p className="text-base leading-snug text-gray-800">
						Kami mendeteksi bahwa anda ingin mengakes fitur yang dilarang. <b className="text-red-500 uppercase">Anda tidak diizinkan</b>{' '}
						untuk mengakes fitur tersebut.
					</p>
				)
			});
			return false;
		}
	})
);

serviceWorker.register();
