import { Button, Form, Input } from 'antd';
import React, { useEffect, useRef } from 'react';
import { HiMiniLockClosed, HiMiniUser } from 'react-icons/hi2';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

export default function DefaultLoginPage() {
	const dispatch = useDispatch();
	const history = useHistory();
	const passwordRef = useRef(null);
	const emailRef = useRef(null);

	const { loading = false } = useSelector((state) => state?.auth);

	const [form] = Form.useForm();

	const onFinish = (values) => {
		const payload = {
			email: values.email || '',
			password: window.btoa(values.password || '')
		};

		dispatch({
			type: 'auth/LOGIN_POS',
			payload,
			history,
			reset: () => form.resetFields()
		});
	};

	const onFinishFailed = (errorInfo) => {
		console.log('Failed:', errorInfo);
	};

	const onKeyDown = (e) => {
		if (e.key === 'Enter' && document.activeElement === emailRef.current.input) {
			emailRef.current.blur();
			passwordRef.current.focus();
			return;
		}

		if (e.key === 'Enter' && document.activeElement === passwordRef.current.input) {
			emailRef.current.blur();
			passwordRef.current.focus();

			onFinish(form.getFieldsValue());

			return;
		}

		if (e.key === 'Enter') {
			form.submit();
		}

		if (e.key === 'Escape') {
			handleCancel();
		}

		if (e.key === 'ArrowUp') {
			emailRef.current.focus();
		}

		if (e.key === 'ArrowDown') {
			passwordRef.current.focus();
		}
	};

	const handleCancel = () => {
		emailRef.current.focus();
		form.resetFields();
	};

	useEffect(() => {
		if (emailRef && emailRef.current) {
			emailRef.current.input.value = '';
			emailRef.current.focus();
		}
	}, [emailRef]);

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, [form]);

	return (
		<div className="w-full">
			
			<div className="mt-10 w-full">
				<Form
					form={form}
					name="login"
					layout="vertical"
					initialValues={{
						remember: true
					}}
					onFinish={onFinish}
					onFinishFailed={onFinishFailed}
					autoComplete="off"
					autoSave="off"
					autoCorrect="off"
					aria-autocomplete="none"
					style={{ maxWidth: 'none', width: '100%' }}>
					<Form.Item
						label="Username"
						name="email"
						rules={[
							{
								required: true,
								message: 'Please input your email!'
							}
						]}>
						<Input
							ref={emailRef}
							type="email"
							autoFocus
							autoComplete="off"
							autoSave="off"
							autoCorrect="off"
							placeholder="Username"
							className="w-full h-14 rounded-md"
							prefix={<HiMiniUser className="text-lg text-gray-500" />}
						/>
					</Form.Item>

					<Form.Item
						label="Password"
						name="password"
						rules={[
							{
								required: true,
								message: 'Please input your password!'
							}
						]}>
						<Input.Password
							ref={passwordRef}
							type="password"
							autoComplete="off"
							autoSave="off"
							autoCorrect="off"
							placeholder="********"
							className="w-full h-14 rounded-md"
							prefix={<HiMiniLockClosed className="text-lg text-gray-500" />}
						/>
					</Form.Item>

					<Form.Item>
						<div className="w-full h-auto flex items-center gap-3 mt-5">
							<Button
								disabled={loading}
								loading={loading}
								type="primary"
								htmlType="submit"
								className="btn-page rounded-md capitalize text-lg">
								Sign In
							</Button>
						</div>
					</Form.Item>
				</Form>
			</div>
		</div>
	);
}
