import axios from 'axios';
import { Config as CONFIG } from '../../utils/api/config';
import { privateAccessHeaders, privateHeaders, publicHeaders } from '../../utils/api/headers';

export const API_MENU_LIST = async () => {
	const { data } = await axios.get(CONFIG.API_MENU_LIST, publicHeaders());

	return { ...data };
};

export const API_CUSTOMER_LIST = async () => {
	const { data } = await axios.get(CONFIG.API_CUSTOMER_LIST, publicHeaders());

	return { ...data };
};

export const API_BILL_NO = async (payload) => {
	const { data } = await axios.get(CONFIG.API_BILL_NO, privateHeaders());
	return { ...data };
};

export const API_STORE_TRANSACTION = async (payload) => {
	const { data } = await axios.post(CONFIG.API_STORE_TRANSACTION, payload, privateHeaders());
	return { ...data };
};

export const API_RETURN_TRANSACTION = async (payload) => {
	const { data } = await axios.post(CONFIG.API_RETURN_TRANSACTION, payload, privateHeaders());
	return { ...data };
};

export const API_HOLD_TRANSACTION = async (payload) => {
	const { data } = await axios.post(CONFIG.API_HOLD_TRANSACTION, payload, privateHeaders());
	return { ...data };
};

export const API_HOLD_LIST_TRANSACTION = async () => {
	const { data } = await axios.get(CONFIG.API_HOLD_LIST_TRANSACTION, privateHeaders());

	return data;
};

export const API_HOLD_TRANSACTION_DETAIL = async (payload) => {
	const { data } = await axios.get(`${CONFIG.API_HOLD_TRANSACTION_DETAIL}?billNo=${payload?.billNo}`, privateHeaders());

	return data;
};

export const API_HOLD_DELETE_TRANSACTION = async (payload) => {
	const { data } = await axios.delete(CONFIG.API_HOLD_DELETE_TRANSACTION, {
		...privateHeaders(),
		data: payload
	});

	return data;
};

export const API_TRANSACTION_HIST_LIST = async ({ dateFrom = '', to = '' }) => {
	const API = `${CONFIG.API_TRANSACTION_HIST_LIST}?from=${dateFrom}&to=${to}`;

	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};

export const API_TRANSACTION_HIST_CUSTOMER = async (payload) => {
	const API = `${CONFIG.API_TRANSACTION_HIST_CUSTOMER}?c=${payload.custCode}from=${payload.from}&to=${payload.to}`;

	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};

export const API_TRANSACTION_HIST_DETAIL = async (payload) => {
	const API = `${CONFIG.API_TRANSACTION_HIST_DETAIL}?r=${payload.billNo}`;

	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};

export const API_TRANSACTION_CLOSING_DAY = async () => {
	const API = `${CONFIG.API_TRANSACTION_CLOSING_DAY}`;

	const { data } = await axios.get(API, privateAccessHeaders());
	return { ...data };
};

export const API_TRANSACTION_OPENING_SHIFT = async (payload) => {
	const API = `${CONFIG.API_TRANSACTION_OPENING_SHIFT}`;
	const { data } = await axios.post(API, payload, privateAccessHeaders());

	return data;
};

export const API_TRANSACTION_CLOSING_SHIFT = async (payload) => {
	const API = `${CONFIG.API_TRANSACTION_CLOSING_SHIFT}`;

	const { data } = await axios.post(API, payload, privateAccessHeaders());
	return data;
};

export const API_TRANSACTION_TENDER_MEDIA = async () => {
	const { data } = await axios.get(CONFIG.API_TENDER_MEDIA, publicHeaders());
	return data;
};

export const API_TRANSACTION_SEARCH_MASTER = async (payload) => {
	const { data } = await axios.get(`${CONFIG.API_SEARCH_MASTER}?q=${payload?.query || ''}`, publicHeaders());

	return data;
};

export const API_AUTH = {
	API_MENU_LIST,
	API_CUSTOMER_LIST,
	API_BILL_NO,
	API_STORE_TRANSACTION,
	API_RETURN_TRANSACTION,
	API_HOLD_TRANSACTION,
	API_HOLD_LIST_TRANSACTION,
	API_HOLD_TRANSACTION_DETAIL,
	API_HOLD_DELETE_TRANSACTION,

	API_TRANSACTION_HIST_LIST,
	API_TRANSACTION_HIST_CUSTOMER,
	API_TRANSACTION_HIST_DETAIL,

	API_TRANSACTION_CLOSING_DAY,
	API_TRANSACTION_OPENING_SHIFT,
	API_TRANSACTION_CLOSING_SHIFT
};
