import NProgress from 'nprogress';
import React, { Fragment } from 'react';
import { FullScreen } from 'react-full-screen';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { Redirect, withRouter } from 'react-router-dom';
import LoginLayout from './Login';
import MainLayout from './Main/index_v1';
import PosLayout from './Pos';
import PublicLayout from './Public';

const Layouts = {
	public: PublicLayout,
	login: LoginLayout,
	main: MainLayout,
	pos: PosLayout
};

@withRouter
@connect(({ auth }) => ({
	auth
	// menuTopData: menu.menuTopData,
}))
class IndexLayout extends React.PureComponent {
	previousPath = '';

	componentDidUpdate(prevProps) {
		const { location } = this.props;
		const { prevLocation } = prevProps;
		if (location !== prevLocation) {
			window.scrollTo(0, 0);
		}
	}

	render() {
		const {
			children,
			location: { pathname, search },
			auth: { authorized = false, loading = false, isLog = false }
		} = this.props;

		// NProgress Management
		const currentPath = pathname + search;

		if (currentPath !== this.previousPath) {
			NProgress.start();
		}

		setTimeout(() => {
			NProgress.done();
			this.previousPath = currentPath;
		}, 300);

		// Layout Rendering
		const getLayout = () => {
			if (pathname === '/') {
				return 'public';
			}
			if (/^\/auth(?=\/|$)/i.test(pathname)) {
				return 'login';
			}
			if (pathname === '/pos/auth') {
				return 'pos';
			}
			return 'main';
		};

		// const getMenu = () => {
		//   return '/dashboard'
		// }

		const Container = Layouts[getLayout()];
		const isUserAuthorized = authorized;
		const isUserLog = isLog;
		const isLoginLayout = getLayout() === 'login';
		// const isDefaultMenu = getMenu() === '/dashboard' ? '/dashboard' : getMenu().url

		const BootstrappedLayout = () => {
			// redirect to login page if current is not login page and auth not authorized
			if (!isUserLog && !isUserAuthorized && isLoginLayout) {
				return <Redirect to={{ pathname: '/pos/auth/default' }} />;
			}

			// redirect to main dashboard when auth on login page and authorized
			if (isLoginLayout && isUserAuthorized && !isUserLog) {
				return <Redirect to={{ pathname: '/dashboard' }} />;
			}

			// in other case render previously set layout
			return <Container>{children}</Container>;
		};

		return (
			<Fragment>
				<Helmet titleTemplate="SCAP POS RETAIL" title="SCAP POS RETAIL" />
				<FullScreen handle={{ active: true }} onChange={console.log}>
					{BootstrappedLayout()}
				</FullScreen>
			</Fragment>
		);
	}
}

export default IndexLayout;
