import {all, takeEvery, put, call} from "redux-saga/effects";

import actions from "./actions";
import {

  API_REPORT_SALES_CUSTOMER,
  API_REPORT_SALES_ITEM,
  API_REPORT_SALES_BY_CUSTOMER,

} from "./apis";


export function* REPORT_SALES_CUSTOMER({payload}) {
  yield put({
    type: actions.SET_STATE,
    payload: {
      loadingReportCustomer: true,
    }
  })

  try {
    const result = yield call(API_REPORT_SALES_CUSTOMER, payload)

    const { acknowledge, data} = result
    if(acknowledge === 1) {
      yield put({
        type: actions.SET_STATE,
        payload: {
          reportSalesCustomer: data
        }
      })
    }


  } catch (e) {
    console.log(e)
  } finally {
    yield put({
      type: actions.SET_STATE,
      payload: {
        loadingReportCustomer: false,
      }
    })
  }
}

