export default function* rootSaga() {

  yield all([

    takeEvery(actions.REPORT_SALES_CUSTOMER, REPORT_SALES_CUSTOMER),
    takeEvery(actions.REPORT_SALES_BY_CUSTOMER, REPORT_SALES_BY_CUSTOMER),
    takeEvery(actions.REPORT_SALES_ITEM, REPORT_SALES_ITEM),
  ])

}
