import { all, call, put, takeEvery } from 'redux-saga/effects';

import { notification } from 'antd';
import actions from './actions';
import { API_SYNC_DATA_MASTER, API_SYNC_DATA_SALES } from './apis';

export function* SYNC_DATA_MASTER({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loadingSync: true
		}
	});

	try {
		const result = yield call(API_SYNC_DATA_MASTER, payload);

		const { acknowledge, message } = result;

		if (acknowledge === 1) {
			yield put({
				type: actions.SET_STATE,
				payload: {
					loadingSync: false
				}
			});

			yield put({
				type: 'transaction/MENU_LIST',
			})

			yield put({
				type: 'transaction/CUSTOMER_LIST'
			});

			yield put({
				type: 'auth/property'
			})

			yield put({
				type: 'transaction/TENDER_MEDIA'
			})

			notification.success({
				message: 'Berhasil!',
				description: 'Aplikasi anda sudah berhasil tersinkronisasi. Semua data sudah diperbarui.'
			});
		}

		if (acknowledge === 0) {
			notification.error({
				message
			});
		}
	} catch (e) {
		console.log(e);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingSync: true
			}
		});
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingSync: false
			}
		});
	}
}

export function* SYNC_DATA_SALES({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loadingUpdateSales: true
		}
	});

	try {
		const result = yield call(API_SYNC_DATA_SALES, payload);

		const { acknowledge, message } = result;

		if (acknowledge === 1) {
			yield put({
				type: actions.SET_STATE,
				payload: {
					loadingUpdateSales: false
				}
			});

			notification.success({
				message: 'Berhasil!!!',
				description: 'Data transaksi anda saat ini sudah berhasil diupload.'
			});
		}

		if (acknowledge === 0) {
			notification.error({
				message: 'Gagal!',
				description: message
			});
		}
	} catch (e) {
		console.log(e);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingUpdateSales: false
			}
		});
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingUpdateSales: false
			}
		});
	}
}

export default function* rootSaga() {
	yield all([takeEvery(actions.SYNC_DATA_MASTER, SYNC_DATA_MASTER), takeEvery(actions.SYNC_DATA_SALES, SYNC_DATA_SALES)]);
}
