/*
 * Component Pages untuk diload di client side
 * */
const routes = [
	{
		path: '/auth/login',
		component: loadable(() => import('./layouts/Pos')),
		exact: true
	},
	{
		path: '/auth/register',
		component: loadable(() => import('./pages/Auth/Register')),
		exact: true
	},
	{
		path: '/auth/verification',
		component: loadable(() => import('./pages/Auth/OTP/indexPass')),
		exact: true
	},
	{
		path: '/dashboard',
		component: loadable(() => import('./pages/Dashboard')),
		exact: true
	},
	{
		path: '/pos/history',
		component: loadable(() => import('./pages/POS/orderHistory')),
		exact: true
	},
	{
		path: '/pos/kasir',
		component: loadable(() => import('./pages/POS')),
		exact: true
	},
	{
		path: '/pos/opening-shift',
		component: loadable(() => import('./pages/POS/opening-shift')),
		exact: true
	},
	{
		path: '/pos/closing-shift',
		component: loadable(() => import('./pages/POS/closing-shift')),
		exact: true
	},
	{
		path: '/pos/kasir/closing',
		component: loadable(() => import('./pages/POS/close-day')),
		exact: true
	},
	{
		path: '/pos/report',
		component: loadable(() => import('./pages/POS/Report')),
		exact: true
	},
	{
		path: '/pos/report/customer',
		component: loadable(() => import('./pages/POS/Report/DetailByCustomer/reportDetailCustomer')),
		exact: true
	},
	{
		path: '/pos/report/customerWeb',
		component: loadable(() => import('./pages/POS/Report/DetailByCustomerWeb/reportDetailCustomer')),
		exact: true
	},
	{
		path: '/pos/report/detail',
		component: loadable(() => import('./pages/POS/Report/DetailItem')),
		exact: true
	},

	{
		path: '/pos/options',
		component: loadable(() => import('./pages/POS/options')),
		exact: true
	},

	{
		path: '/pos-module/property',
		component: loadable(() => import('./pages/PosModule/Property/v1/index')),
		exact: true
	},
	{
		path: '/pos/payment',
		component: loadable(() => import('./pages/POS/payment')),
		exact: true
	},
	{
		path: '/pos/setup',
		component: loadable(() => import('./pages/POS/setup')),
		exact: true
	}
];

const usePageTracking = () => {
	const location = useLocation();

	useEffect(() => {
		ReactGA.send({ hitType: 'pageview', page: location.pathname + location.search });
	}, [location]);
};

const RouterIndex = ({ history }) => {
	usePageTracking();

	return (
		<IndexLayout>
			<Router>
				<Switch>
					<Route exact path="/" render={() => <Redirect to="/pos/auth" />} />
					{routes.map((route) => (
						<Route key={route.path} path={route.path} component={route.component} exact={route.exact} />
					))}

					<Route path="/pos" exact render={() => <Redirect to="/pos/auth/default" />} />
					<Route
						path="/pos/auth"
						render={({ match: { url } }) => (
							<POSLayoutPage>
								<Switch>
									<Route path={`${url}/default`} component={loadable(() => import('./layouts/Pos/DefaultLoginPage'))} exact />
									<Route path={`${url}/pin`} component={loadable(() => import('./layouts/Pos/PinPosPage'))} exact />
									<Route path="*" render={() => <Redirect to={`${url}/default`} />} />
								</Switch>
							</POSLayoutPage>
						)}
					/>
				</Switch>
			</Router>
		</IndexLayout>
	);
};

export default RouterIndex;
