import actions from './actions';

const initialState = {
	contentStatus: 'isMenu',
	menuList: [],
	customerList: [],
	billNo: '',
	outletCode: '',
	billDate: '',
	transactionList: [],
	transactionHolds: [],
	data: [],
	tenderMedia: [],
	selectedTransaction: {},
	selectedRowTransaction: {},
	searchResult: [],

	loading: false,
	loadingList: false,
	loadingReport: false,
	loadingDetail: false,

	isSuccess: false,

	historyTransactionList: [],
	historyTransactionDetail: [],
	historyTransactionReport: []
};

export default function userReducer(state = initialState, action) {
	switch (action.type) {
		case actions.SET_STATE:
			return { ...state, ...action.payload };
		default:
			return state;
	}
}
