import { notification } from 'antd';

export const currencyFormat = (text = '', tf = 2) => {
	return Number(text)
		.toFixed(tf)
		.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
};

export const utilities = {
	currencyFormat
};

export const getServerConnection = async (event) => {
	const filePath = '/server.txt';

	try {
		const response = await fetch(filePath);

		if (!response.ok) {
			notification.error({
				message: 'Error!',
				description: 'Server not found! Please reload the page.'
			});

			throw new Error('Network response was not ok');
		}

		const text = await response.text();
		const resultSplit = text.split('\n');
		const splitted = resultSplit.slice(2, 4);
		const url = splitted.slice(0, 1)[0];

		// const encryptedUrl = encrypted(url);

		if (url) {
			localStorage.setItem('appUrl', url);
			notification.success({
				message: 'Berhasil',
				description: `Setup server ditemukan. Anda terhubung ke ${url}.`
			});
		} else {
			console.error('Encryption failed, URL not saved to localStorage');
		}
	} catch (error) {
		console.log(error);
	}
};

export const getAppUrl = () => {
	const appUrl = localStorage.getItem('appUrl') || '';
	// const decryptUrl = decrypted(appUrl);

	return appUrl;
};
