import axios from "axios";
import { Config as CONFIG } from '../../utils/api/config';
import { privateHeaders } from '../../utils/api/headers';

export const API_SYNC_DATA_MASTER = async (payload) => {
	const { data } = await axios.get(`${CONFIG.API_SYNC_DATA_MASTER}`, privateHeaders());

	return { ...data };
};

export const API_SYNC_DATA_SALES = async (payload) => {
	const API = `${CONFIG.API_SYNC_DATA_SALES}?f=${payload.from}&t=${payload.to}`;

	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};


export const API_SYNC = {
  API_SYNC_DATA_MASTER,
  API_SYNC_DATA_SALES,
}
