import actions from './actions'


const initialState = {
  contentStatus: 'isMenu',
  reportSalesCustomer:[],
  loadingReportCustomer:false,

  reportSalesItem:[],
  loadingReportItem:false,

  reportSalesCustomerWeb:[],
  loadingReportCustomerWeb:false,


}

export default function userReducer(state = initialState, action) {
  switch (action.type) {
    case actions.SET_STATE:
      return { ...state, ...action.payload }
    default:
      return state
  }
}
