import axios from "axios";
import { Config as CONFIG } from '../../utils/api/config';
import { privateHeaders, publicHeaders } from '../../utils/api/headers';

export const API_AUTH_LOGIN = (payload) => {
	console.log('CALL DARI SAGAS', payload);
};

export const API_AUTH_REGISTER = async (payload) => {
	const { data } = await axios.post(CONFIG.API_USER_REGISTER, payload);

	return { ...data };
};

export const API_AUTH_LOGIN_PIN = async (payload) => {
	const { data } = await axios.post(CONFIG.API_AUTH_LOGIN_POS, payload);

	return data;
};

export const API_AUTH_LOGIN_POS = async (payload) => {
	const { data } = await axios.post(CONFIG.API_AUTH_LOGIN_POS, payload);

	return { ...data };
};

export const API_AUTH_PROPERTY = async () => {
	const { data } = await axios.get(CONFIG.API_PROPERTY, publicHeaders());

	return data;
};

export const API_AUTH_USER_ACCESS = async (payload) => {
	const { data } = await axios.post(CONFIG.API_AUTH_USER_ACCESS, payload, privateHeaders());

	return data;
};

export const API_UPDATE_PROPERTY = async (payload) => {
	const { data } = await axios.post(CONFIG.API_UPDATE_PROPERTY, payload, privateHeaders());

	return data;
};

export const API_AUTH = {
	API_AUTH_LOGIN,
	API_AUTH_REGISTER,
	API_AUTH_LOGIN_PIN,
	API_AUTH_LOGIN_POS,
	API_AUTH_PROPERTY,
	API_UPDATE_PROPERTY
};
