import axios from "axios";
import { Config as CONFIG } from '../../utils/api/config';
import { privateHeaders } from '../../utils/api/headers';

export const API_REPORT_SALES_CUSTOMER = async (payload) => {
	const API = `${CONFIG.API_REPORT_SALES_CUSTOMER}?c=${payload.custCode}&from=${payload.from}&to=${payload.to}`;

	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};

export const API_REPORT_SALES_ITEM = async (payload) => {
	const API = `${CONFIG.API_REPORT_SALES_ITEM}?from=${payload.from}&to=${payload.to}`;

	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};

export const API_REPORT_SALES_BY_CUSTOMER = async ({ from, to, l = 'ALL', c = 'ALL', isType = 'RK' }) => {
	const API = `${CONFIG.API_REPORT_SALES_BY_CUSTOMER}?from=${from}&to=${to}&l=${l}&c=${c}&isType=${isType}`;
	const { data } = await axios.get(API, privateHeaders());
	return { ...data };
};




export const API_AUTH = {
  API_REPORT_SALES_CUSTOMER,
  API_REPORT_SALES_ITEM,
  API_REPORT_SALES_BY_CUSTOMER
}
