import { all, } from 'redux-saga/effects'

import auth from '../redux/auth/sagas'
import transaction from '../redux/transaction/sagas'
import sync from '../redux/sync/sagas'
import report from "../redux/report/sagas"

function* rootSaga() {

  yield all([
    auth(),
    transaction(),

    sync(),
    report()
  ])

}

export default rootSaga
