import { Image } from 'antd';
import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import DefaultLoginPage from './DefaultLoginPage';

export default function POSLayoutPage({ children }) {
	const history = useHistory();
	const { isLog: isAuthLog, authorized: isAuthorized } = useSelector((state) => state?.auth);

	useEffect(() => {
		const app = localStorage.getItem('app');
		if (app && app !== null) {
			const json = JSON.parse(app);
			const { isLog = false, authorized = false } = json;

			if ((isLog && authorized) || (isAuthLog && isAuthorized)) {
				history.push('/pos/kasir');
				return;
			} else if ((!isLog && authorized) || (!isAuthLog && isAuthorized)) {
				history.push('/pos/auth/default');
				return;
			}

			history.push('/auth/login');
		}
	}, []);

	return (
		<div className="w-screen h-screen overflow-hidden">
			{/* <Button
				type="primary"
				shape="circle"
				size="large"
				icon={<FaGear />}
				className="absolute top-5 right-5 flex items-center justify-center z-50"
				onClick={() => history.push('/pos/setup')}
			/> */}

			<div className="w-full grid grid-cols-12">
				<div className="col-span-8">
					<div className="w-full h-screen bg-no-repeat bg-cover" style={{ backgroundImage: 'url(/pos.jpg)' }}>
						<div className="w-full h-full" style={{ background: 'rgba(0, 0, 0, 0.3)' }}>
							<div className="w-full h-full flex items-center justify-center flex-col">{/*  */}</div>
						</div>
					</div>
				</div>
				<div className="col-span-4">
					<div className="w-full relative">
						<div className="w-full h-screen p-7 bg-transparent">
							<div className="w-full h-full flex items-center justify-center flex-col">
								<div className="w-full flex flex-col items-center justify-center">
									<Image src="/logo-2.png" className="w-40 h-auto object-cover" preview={false} />
									<h1 className="text-xl text-gray-800 font-semibold mt-1">Scap Point of Sales</h1>
									{/* <p className="text-gray-600">{Config.APP_VERSION}</p> */}
								</div>
								<DefaultLoginPage />
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
