import { all, call, put, takeEvery } from 'redux-saga/effects';

import { notification } from 'antd';
import actions from './actions';
import {
	API_AUTH_LOGIN,
	API_AUTH_LOGIN_PIN,
	API_AUTH_LOGIN_POS,
	API_AUTH_PROPERTY,
	API_AUTH_REGISTER,
	API_AUTH_USER_ACCESS,
	API_UPDATE_PROPERTY
} from './apis';

export function* AUTH_LOGIN({ payload }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_AUTH_LOGIN, payload);
		// history.push('/dashboard')
		console.log('RESULT', result);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false,
				authorized: true
			}
		});
		localStorage.setItem('authorized', true);
	} catch (e) {
		console.log(e);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: true
			}
		});
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: true
			}
		});
	}
}

export function* AUTH_REGISTER({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loadingOtp: true
		}
	});

	try {
		const result = yield call(API_AUTH_REGISTER, payload);
		const { acknowledge } = result;

		console.log('RESULT', result);

		if (acknowledge === 1) {
			// GANTI HALAMAN JIKA SUKSES MENGIRIM OTP
			history.push('/auth/login');
			yield put({
				type: actions.SET_STATE,
				payload: {
					loadingOtp: false
				}
			});
		}
	} catch (e) {
		console.log(e);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingOtp: true
			}
		});
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingOtp: true
			}
		});
	}
}

export function* LOAD_CURRENT_ACCOUNT() {
	const app = JSON.parse(localStorage.getItem('app') || '{}');
	const { authorized = false, isLog = false } = app;

	const token = localStorage.getItem('token') ? localStorage.getItem('token') : '';
	const username = localStorage.getItem('username') ? localStorage.getItem('username') : '';
	const dateOpening = localStorage.getItem('dateOpening') ? localStorage.getItem('dateOpening') : '';

	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});
	yield put({
		type: actions.SET_STATE,
		payload: {
			token,
			username,
			authorized,
			isLog,
			config: {
				dateOpening
			}
		}
	});
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: false
		}
	});
}

export function* AUTH_LOGOUT() {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false,
				authorized: false
			}
		});
		localStorage.setItem('authorized', false);
	} catch (e) {
		console.log(e);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* AUTH_LOGIN_PIN({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_AUTH_LOGIN_PIN, payload);

		const { acknowledge, data, /* isLog, */ message } = result;

		if (acknowledge === 1) {
			const payloadResult = {
				isLog: true,
				authorized: true,
				property: {},
				user: {
					id: data?.userID || '',
					username: data?.userName || '',
					role: 'kasir',
					pin: window.btoa(payload?.pinCode)
				}
			};

			yield put({
				type: actions.SET_STATE,
				payload: {
					loading: false,
					authorized: true,
					isLog: true
				}
			});

			yield put({
				type: actions.AUTH_PROPERTY,
				payload: payloadResult
			});

			localStorage.setItem('app', JSON.stringify(payloadResult));
			history.push('/pos/kasir');

			notification.success({
				message,
				description: data.UserName || ''
			});

			return;
		}

		localStorage.setItem('app', null);
		notification.error({
			message: 'Terjadi Kesalahan!',
			description: message
		});
	} catch (e) {
		console.log(e);

		localStorage.setItem('app', null);
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: true
			}
		});
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* PROPERTY() {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_AUTH_PROPERTY);
		const { acknowledge = 0, data = {} } = result;

		if (acknowledge === 1) {
			let app = JSON.parse(localStorage.getItem('app')) || {};
			app = {
				...app,
				property: { ...data, NewData: true }
			};

			localStorage.setItem('app', JSON.stringify(app));
		}
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* AUTH_LOGOUT_POS({ history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const payload = {
			isLog: false,
			authorized: true,
			user: {},
			property: {},
			roles: [],
			token: '',
			cashier: {}
		};

		yield put({
			type: actions.SET_STATE,
			payload: {
				isLog: false,
				authorized: true,
				isGrantedAccess: false
			}
		});

		yield put({
			type: 'transaction/SET_STATE',
			payload: {
				menuList: [],
				customerList: [],
				billNo: '',
				outletCode: '',
				billDate: '',
				transactionList: [],
				data: []
			}
		});

		localStorage.setItem('app', JSON.stringify(payload));
		history.push('/pos/auth');
	} catch (error) {
		console.log('Logout Error! ', error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* AUTH_LOGIN_POS({ payload, history, reset }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_AUTH_LOGIN_POS, payload);
		const { acknowledge = 0, data = {}, isLog = false, token = '', roles = [], message = '', isOpenShift = 0, shiftId = 0 } = result;

		if (acknowledge === 1 && isLog) {
			let result = {
				isLog,
				authorized: true,
				user: {
					userId: data?.userID || '',
					username: data?.userName || ''
				},
				roles,
				token,
				tokenAccess: '',
				property: {},
				cashier: {}
			};

			yield put({
				type: actions.SET_STATE,
				payload: {
					isLog,
					authorized: true
				}
			});

			yield put({
				type: actions.AUTH_PROPERTY
			});

			yield put({
				type: 'sync/SYNC_DATA_MASTER'
			});

			localStorage.setItem('app', JSON.stringify(result));
			if (isOpenShift === 1) {
				let app = JSON.parse(localStorage.getItem('app') || '{}');

				app = {
					...app,
					cashier: {
						isOpening: isOpenShift === 1,
						isClosing: false,
						openingCash: 0,
						closingCash: 0,
						shiftId: shiftId,
						shiftDate: app?.property?.refDate
					}
				};

				localStorage.setItem('app', JSON.stringify(app));
				history.push('/pos/kasir');

				notification.success({
					message: 'Berhasil',
					description: 'Shift telah terbuka, anda dapat melanjukan transaksi.'
				});
				return;
			}

			notification.success({
				message: 'Login Berhasil',
				description: message
			});

			history.push('/pos/opening-shift');
			return;
		}

		notification.error({
			message: 'Login Gagal',
			description: message
		});
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});

		reset();
	}
}

export function* AUTH_USER_ACCESS({ payload, history = () => {} }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	let app = JSON.parse(localStorage.getItem('app') || '{}');

	try {
		const result = yield call(API_AUTH_USER_ACCESS, payload);
		const { acknowledge = 0, isAuthorized = 0, token = '' } = result;

		if (acknowledge === 1 && isAuthorized === 1) {
			app = {
				...app,
				tokenAccess: token
			};

			yield put({
				type: actions.SET_STATE,
				payload: {
					isGrantedAccess: true
				}
			});

			localStorage.setItem('app', JSON.stringify(app));

			notification.success({
				message: 'Verified!',
				description: 'Akun berhasil di verifikasi'
			});
		} else {
			yield put({
				type: actions.SET_STATE,
				payload: {
					isGrantedAccess: false
				}
			});

			notification.error({
				message: 'Gagal',
				description: 'Akun yang anda kirimkan tidak memiliki akses. Silahkan coba lagi atau Login ulang'
			});

			history();
		}
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* AUTH_UPDATE_PROPERTY({ payload }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const response = yield call(API_UPDATE_PROPERTY, payload);
		const { acknowledge = 0, message = '' } = response || {};

		if (acknowledge === 1) {
			yield put({
				type: actions.SET_STATE,
				payload: {
					isUpdatedProperty: true
				}
			});

			yield put({
				type: 'sync/SYNC_DATA_MASTER'
			});

			notification.success({
				message: 'Update Berhasil!',
				description: 'Data sudah berhasil terupdate! Silahkan sinkronisasi data terlebih dahulu.'
			});
			return;
		}

		yield put({
			type: actions.SET_STATE,
			payload: {
				isUpdatedProperty: false
			}
		});

		notification.error({
			message: 'Update Gagal!',
			description: message
		});
	} catch (error) {
		console.log('Error', error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export default function* rootSaga() {
	yield all([
		takeEvery(actions.AUTH_LOGIN, AUTH_LOGIN),
		takeEvery(actions.AUTH_REGISTER, AUTH_REGISTER),
		takeEvery(actions.AUTH_LOGOUT, AUTH_LOGOUT),
		takeEvery(actions.AUTH_LOGIN_PIN, AUTH_LOGIN_PIN),
		takeEvery(actions.AUTH_PROPERTY, PROPERTY),
		takeEvery(actions.AUTH_LOGOUT_POS, AUTH_LOGOUT_POS),
		takeEvery(actions.AUTH_LOGIN_POS, AUTH_LOGIN_POS),
		takeEvery(actions.AUTH_USER_ACCESS, AUTH_USER_ACCESS),
		takeEvery(actions.AUTH_UPDATE_PROPERTY, AUTH_UPDATE_PROPERTY),

		LOAD_CURRENT_ACCOUNT()
	]);
}
