export function* TRANSACTION_PAYMENT() {
	yield put({
		type: actions.SET_STATE,
		payload: {
			contentStatus: 'isPayment'
		}
	});
}

export function* TRANSACTION_BACK_MENU() {
	yield put({
		type: actions.SET_STATE,
		payload: {
			contentStatus: 'isMenu'
		}
	});
}

export function* TRANSACTION_MENU_LIST() {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_MENU_LIST);

		const { acknowledge, data } = result;

		if (acknowledge === 1) {
			yield put({
				type: actions.SET_STATE,
				payload: {
					menuList: data.map((el) => ({
						...el,
						upcBarcode: el.UPCbarcode,
						itemCode: el.ItemCode,
						itemName: el.ItemName,
						price: el.SalesPrice,
						label: `${el.UPCbarcode} | ${el.ItemCode} | ${el.ItemName}`,
						value: el.ItemCode
					}))
				}
			});
		}
	} catch (e) {
		console.log(e);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

