export function* TRANSACION_HOLD_DETAIL({ payload, history, onClose, onState }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_HOLD_TRANSACTION_DETAIL, payload);
		const { isLog = false, acknowledge = 0, message = '', data = {} } = result;

		if (acknowledge === 1) {
			const { header = [], detail = [] } = data;

			const details = detail?.map((el, index) => {
				const total = +el.qty * +el.price;

				return {
					...el,
					CateCode: el.cateCode,
					GroupCode: el.groupCode,
					ItemCode: el.menuCode,
					ItemName: el.menuName,
					OnHandQty: '0.00',
					SalesPrice: '0', // BELUM ADA DI PAYLOAD RES
					Timbang: '0', // BELUM ADA DI PAYLOAD RES
					UPCbarcode: '0', // BELUM ADA DI PAYLOAD RES
					Unitprice: '0', // BELUM ADA DI PAYLOAD RES
					UomStock: '', // BELUM ADA DI PAYLOAD RES
					amount: el.amount,
					isActive: '1',
					itemCode: el.menuCode,
					itemName: el.menuName,
					label: `${el.menuCode} - ${el.menuName} - ${currencyFormat(el.price)}`,
					price: el.price,
					qty: el.qty,
					total: total,
					upcBarcode: '', // BELUM ADA DI PAYLOAD RES
					updated_at: el.orderTime,
					value: el.menuCode
				};
			});

			yield put({
				type: actions.SET_STATE,
				payload: {
					billNo: header[0]?.billNo,
					outletCode: header[0]?.outletCode,
					billDate: header[0]?.billDate,
					transactionList: details,
					data: details
				}
			});

			onClose();
			onState({
				data: details
			});

			notification.success({
				message: 'Berhasil!',
				description: 'Transaksi anda sudah berhasil dipulihkan.'
			});

			yield put({
				type: actions.TRANSACTION_DELETE_HOLD,
				payload,
				history
			});
		} else if (!isLog) {
			yield put({
				type: 'auth/LOGOUT_POS',
				history
			});
		} else {
			notification.error({
				message: 'Terjadi Kesalahan!',
				description: message
			});
		}
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

