export function* TRANSACTION_OPENING_SHIFT({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		let app = JSON.parse(localStorage.getItem('app') || '{}');
		const result = yield call(API_TRANSACTION_OPENING_SHIFT, payload);
		const { acknowledge = 0, message = '', isLog = false, shiftId = '', isShiftOpen = 0 } = result;

		if (isLog) {
			history.push('/pos/auth');
			return;
		}

		if (acknowledge === 1 && isShiftOpen === 1) {
			const payloadData = {
				...app,
				tokenAccess: '',
				cashier: {
					isOpening: isShiftOpen === 1,
					isClosing: false,
					openingCash: payload?.beginCash || 0,
					closingCash: 0,
					shiftId: shiftId,
					shiftDate: app?.property?.refDate
				}
			};

			yield put({
				type: 'auth/SET_STATE',
				payload: {
					isGrantedAccess: false
				}
			});

			localStorage.setItem('app', JSON.stringify(payloadData));
			history.push('/pos/kasir');
			return;
		}

		notification.error({
			message: 'Opening Shift Gagal!',
			description: message
		});
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* TRANSACTION_CLOSING_SHIFT({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		let app = JSON.parse(localStorage.getItem('app') || '{}');
		const result = yield call(API_TRANSACTION_CLOSING_SHIFT, payload);
		const { acknowledge = 0, message = '' } = result;

		if (acknowledge === 1) {
			history.push('/pos/kasir');

			yield put({
				type: 'auth/SET_STATE',
				payload: {
					isGrantedAccess: false
				}
			});

			app = {
				...app,
				tokenAccess: '',
				cashier: {
					...app.cashier,
					isOpening: false,
					isClosing: true,
					closingCash: payload?.closeCash || 0
				}
			};

			localStorage.setItem('app', JSON.stringify(app));

			notification.success({
				message: 'Berhasil',
				description: message
			});

			return;
		}

		notification.error({
			message: 'Closing Shift Gagal',
			description: message
		});
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* TRANSACTION_TENDER_MEDIA() {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_TRANSACTION_TENDER_MEDIA);
		const { data = [] } = result;

		// sort by paidType from small and largest number
		data.sort((a, b) => {
			return a.paidType - b.paidType;
		});

		yield put({
			type: actions.SET_STATE,
			payload: {
				tenderMedia: data
			}
		});
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export function* TRANSACTION_SEARCH_MASTER({ payload }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loading: true
		}
	});

	try {
		const result = yield call(API_TRANSACTION_SEARCH_MASTER, payload);
		const { acknowledge = 0, data = [] } = result;

		if (acknowledge === 1) {
			if (data?.length === 0) {
				notification.warning({
					message: 'Not Found!',
					description: `${payload?.query}. Data yang anda cari tidak ditemukan. Mohon coba kembali!`
				});
				return;
			}

			yield put({
				type: actions.SET_STATE,
				payload: {
					searchResult: data.map((el) => ({
						...el,
						upcBarcode: el.UPCbarcode,
						itemCode: el.ItemCode,
						itemName: el.ItemName,
						price: el.SalesPrice,
						label: `${el.UPCbarcode} | ${el.ItemCode} | ${el.ItemName}`,
						value: el.ItemCode
					}))
				}
			});

			return;
		}
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loading: false
			}
		});
	}
}

export default function* rootSaga() {
	yield all([
		takeEvery(actions.TRANSACTION_PAYMENT, TRANSACTION_PAYMENT),
		takeEvery(actions.TRANSACTION_BACK_MENU, TRANSACTION_BACK_MENU),
		takeEvery(actions.TRANSACTION_MENU_LIST, TRANSACTION_MENU_LIST),
		takeEvery(actions.TRANSACTION_CUSTOMER_LIST, TRANSACTION_CUSTOMER_LIST),
		takeEvery(actions.TRANSACTION_TENDER_MEDIA, TRANSACTION_TENDER_MEDIA),
		takeEvery(actions.TRANSACTION_SEARCH_MASTER, TRANSACTION_SEARCH_MASTER),

		takeEvery(actions.TRANSACTION_BILL_NO, TRANSACTION_BILL_NO),
		takeEvery(actions.TRANSACTION_STORE, TRANSACTION_STORE),
		takeEvery(actions.TRANSACTION_RETURN, TRANSACTION_RETURN),
		takeEvery(actions.TRANSACTION_HOLD, TRANSACTION_HOLD),
		takeEvery(actions.TRANSACTION_HOLD_LIST, TRANSACION_HOLD_LIST),
		takeEvery(actions.TRANSACTION_HOLD_DETAIL, TRANSACION_HOLD_DETAIL),
		takeEvery(actions.TRANSACTION_DELETE_HOLD, TRANSACION_HOLD_DELETE),

		takeEvery(actions.TRANSACTION_HISTORY_LIST, TRANSACTION_HISTORY_LIST),
		takeEvery(actions.TRANSACTION_HISTORY_DETAIL, TRANSACTION_HISTORY_DETAIL),
		takeEvery(actions.TRANSACTION_CLOSING_DAY, TRANSACTION_CLOSING_DAY),
		takeEvery(actions.TRANSACTION_HISTORY_CUSTOMER, TRANSACTION_HISTORY_CUSTOMER),
		takeEvery(actions.TRANSACTION_OPENING_SHIFT, TRANSACTION_OPENING_SHIFT),
		takeEvery(actions.TRANSACTION_CLOSING_SHIFT, TRANSACTION_CLOSING_SHIFT)
	]);
}
