import React from 'react'
import { Layout } from 'antd'
import { withRouter } from 'react-router-dom'


@withRouter
class MembershipLayout extends React.PureComponent {
  render() {
    const {children} = this.props
    return (
      <Layout>
        <Layout.Content>
          <div>
            <div>{children}</div>
          </div>
        </Layout.Content>
      </Layout>
    )
  }
}

export default MembershipLayout
